const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

export default function UserTable({data}) {
  const classes = useStyles();
  function getTie(a) {
    let date = new Date(a.seconds * 1000)
    return date.toDateString()
  }
  function goTarget(e){
    return <UserDetails data={e}/>
  }
  return (
    <React.Fragment>
      <Title>User</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Badge No</TableCell>
            <TableCell>Added By</TableCell>
            <TableCell>ID</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.data.firstName}</TableCell>
              <TableCell>{row.data.badge}</TableCell>
              <TableCell>{row.data.admin}</TableCell>
              <TableCell  ><Link to= {`/UserTable/${row.id}`} style={{cursor:'pointer', color:'blue'}}>{row.id}</Link></TableCell>

              
            </TableRow>
          ))}
        </TableBody>
      </Table>
      
  
    </React.Fragment>
  );
}
